<template>
  <section class="section">
    <div class="modal" :class="{'is-active': !!error}">
      <div class="modal-background" @click="handleError"></div>
      <div class="modal-content">
        <article class="message is-danger">
          <div class="message-header">
            <p>Signup Error</p>
            <button class="delete" aria-label="delete" @click="handleError"></button>
          </div>
          <div class="message-body">
            {{ error }}
          </div>
        </article>
      </div>
      <button class="modal-close is-large" aria-label="close" @click="handleError"></button>
    </div>
    <div class="modal" :class="{'is-active': success}">
      <div class="modal-background" @click="handleSuccess"></div>
      <div class="modal-content">
        <article class="message is-success">
          <div class="message-header">
            <p>Registration Succeeded</p>
            <button class="delete" aria-label="delete" @click="handleSuccess"></button>
          </div>
          <div class="message-body">
            Your account has been created and linked with your FSEconomy account. Your initial password
            has been sent to the indicated email address. If you can't find the email, please make sure to
            check your spam folder.
          </div>
        </article>
      </div>
      <button class="modal-close is-large" aria-label="close" @click="handleSuccess"></button>
    </div>
    <div class="container">
      <div v-if="isLoading">
        <base-spinner></base-spinner>
      </div>
      <div class="columns is-centered">
        <div class="column is-5-tablet is-5-desktop is-4-widescreen">
          <div class="box">
            <form @submit.prevent="doSignUp">
              <div class="field">
                <label class="label" for="username">FSEconomy Username</label>
                <div class="control has-icons-left">
                  <input class="input" type="text" id="username" placeholder="Username" ref="userInput" v-model.trim="username">
                  <span class="icon is-small is-left">
                    <svg-icon type="mdi" :path="userIcon">&nbsp;</svg-icon>
                  </span>
                </div>
              </div>
              <div class="field">
                <label class="label" for="email">Email Address</label>
                <div class="control has-icons-left">
                  <input class="input" :class="{'is-danger': ko, 'is-success': ok}" type="email" id="email" placeholder="Email Address" v-model.trim="email" @keyup="checkMail">
                  <span class="icon is-small is-left">
                    <svg-icon type="mdi" :path="emailIcon">&nbsp;</svg-icon>
                  </span>
                </div>
                <p class="help is-danger" v-if="ko">Not a valid email address</p>
                <p class="help is-success" v-if="ok">Valid email address</p>
              </div>
              <div class="field">
                <p class="help">
                  By registering an account, you give Stormrose permission to store and process submitted data in
                  accordance with our <router-link to="/privacy" target="_blank">privacy policy</router-link> and
                  confirm you have read and agree to our
                  <router-link to="/terms" target="_blank">terms of service</router-link>.
                </p>
              </div>
              <article class="message is-danger" v-if="!formIsValid">
                <div class="message-body">
                  Please make sure to enter a username and a valid email address. Neither of them can be empty.
                </div>
              </article>
              <div class="field is-grouped">
                <div class="control">
                  <button class="button is-link">Sign Up</button>
                </div>
              </div>
            </form>
          </div>
        </div>
      </div>
    </div>
  </section>
</template>

<script>
import BaseSpinner from "@/components/ui/BaseSpinner";
import SvgIcon from '@jamescoyle/vue-icon';
import {mdiAccount} from '@mdi/js';
import {mdiAt} from '@mdi/js';

function emailIsValid (email) {
  return /^[^\s@]+@[^\s@]+\.[^\s@]+$/.test(email)
}

export default {
  name: "UserSignup.vue",
  components: {
    BaseSpinner,
    SvgIcon
  },
  computed: {
    userIcon() {
      return mdiAccount;
    },
    emailIcon() {
      return mdiAt;
    }
  },
  data() {
    return {
      isLoading: false,
      error: null,
      success: false,
      formIsValid: true,
      username: '',
      email: '',
      ok: false,
      ko: false
    }
  },
  methods: {
    async doSignUp() {
      if (this.username === '' || this.email === '' || !this.email.includes('@')) {
        this.formIsValid = false;
        return;
      }
      this.isLoading = true;
      const actionPayload = {
        username: this.username,
        email: this.email
      };
      try {
        await this.$store.dispatch('auth/register', actionPayload);
        this.success = true;
      } catch (error) {
        this.error = error.message || 'Failed to register, try later.';
      }
      this.isLoading = false;
    },
    handleError() {
      this.error = null;
    },
    handleSuccess() {
      this.success = false;
      const redirectUrl = '/' + (this.$route.query.goto || 'aircraft');
      this.$router.replace(redirectUrl);
    },
    checkMail() {
      if (this.email === '') {
        this.ok = false;
        this.ko = false;
        return;
      }
      if (emailIsValid(this.email)) {
        this.ok = true;
        this.ko = false;
      } else {
        this.ok = false;
        this.ko = true;
      }
    }
  }
}
</script>
